





























import Vue from 'vue';
export default Vue.extend({
    props: {
        isBreak: Boolean,
    },
    data() {
        return {
            input: 'ADD A NEW MISSION...' as string,
        };
    },
    methods: {
        addTask(): void {
            if (this.input === 'ADD A NEW MISSION...' || this.input === '') {
                return;
            }
            this.$emit('addTask', this.input);
            this.input = '';
        },
    },
});
