





































































































































































































import Vue from 'vue';
import CountdownCircle from '../../components/Pomodoro/CountdownCircle.vue';
import InputTask from '../../components/Pomodoro/InputTask.vue';
import moment from 'moment';
import localStorageJs from '@/assets/js/localStorage';
import { mapActions, mapState, mapGetters, mapMutations } from 'vuex';

export default Vue.extend({
    name: 'Pomodora',
    components: {CountdownCircle, InputTask},
    data() {
        return {
            stop: true as boolean,
            time: 1500 as number,
            isBreak: false as boolean,
            interval: 0 as number,
            tomatos: 0 as number,
        };
    },
    mounted() {
        this.$store.dispatch('pomodoro/TASKS_INIT');
        this.$store.dispatch('pomodoro/DONE_INIT');
    },
    computed: {
        ...mapState('pomodoro', [
            'tasks',
            'done',
        ]),
        ...mapGetters('pomodoro', [
            'lineUpTasks',
            'firstTodo',
        ]),
        min(): string {
            const min = Math.floor((this.time * 1000) / (1000 * 60));
            return this.addZeroAndToString(min);
        },
        second(): string {
            const sec = Math.floor((this.time * 1000 % (1000 * 60)) / 1000);
            return this.addZeroAndToString(sec);
        },
    },
    methods: {
        ...mapMutations('pomodoro', [
            'ADD_TASK',
            'ITEM_FINISHED',
        ]),
        toggle(stop: boolean): void {
            this.stop = stop;
            if (this.stop === false) {
                this.interval = this.countDown();
            }
            if (this.stop === true) {
                clearInterval(this.interval);
            }
        },
        countDown(): number {
            return setInterval(() => {
                if (this.stop === false) {
                    this.time -- ;
                    if (this.time <= 0) {
                        this.changeInterval();
                    }
                }
            }, 1000);
        },
        changeInterval(): void {
            this.isBreak = !this.isBreak;
            if (this.isBreak) {
                this.tomatos++;
                this.time = 300;
            }
            if (!this.isBreak) {
                this.time = 1500;
            }
            this.ring();
        },
        addZeroAndToString(num: number): string {
            if (num >= 10) {
                return num.toString();
            }
            return '0' + num.toString();
        },
        ring(): void {
            const audio = new Audio(require('../../assets/Alarm_Clock.mp3'));
            audio.play();
        },
        resetTimer() {
            location.reload();
        },
    },
});
