































































































































import Vue from 'vue';
export default Vue.extend({
    props: {
        stop : Boolean,
        isBreak: Boolean,
    },
    computed: {
        defaultColor(): string {
            if (this.isBreak) {
                return 'blue';
            }
            return 'red';
        },
        circleStyle(): string {
            let bgColor = '';
            if (this.stop) {
                bgColor = 'bg-' + this.defaultColor;
            } else {
                bgColor = 'bg-white';
            }
            return bgColor + ' ' + 'border-color-' + this.defaultColor;
        },
    },
    methods: {
        toggle(): void {
            this.$emit('toggle', !this.stop);
        },
    },
});
